.item {
    margin: auto;
  }
  
  .item .container_img {
    width: max-content;
    margin: auto;
    margin-top: 5vh;
  }
  .item .item_img {
    width: 40vh;
    height: 20vh;
  }
  .item .item_h1 {
    font-family: sans-serif;
    font-size: 24px;
    width: max-content;
    margin: 2vh auto;
  }
  
  .section_item_one {
    width: max-content;
    margin: auto;
  }
  
  .section_item_one p {
    font-family: serif;
    font-size: 18px;
    margin: 2vh;
  }
  
  .section_item_one span {
    font-size: 16px;
    color: rgb(142, 142, 142);
  }
  
  .section_item_two {
    width: max-content;
    margin: auto;
  }
  
  .section_item_two p {
    font-size: 18px;
    font-family: sans-serif;
    margin: 2vh;
  }
  
  .section_item_two span {
    color: rgb(114, 114, 114);
    font-size: 16px;
  }
  
  .border {
    font-size: 18px;
    font-family: sans-serif;
    margin: 2vh auto;
  
  }
  
  .dont_borders {
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: sans-serif;
    font-size: 14px;
    color: rgb(48, 48, 48);
    width: 50%;
    margin: auto;
    margin-top: 3vh;
    padding: 5px;
    background-color: rgb(222, 222, 222);
    border-radius: 10px;
  }
  
  
  @media (min-width: 900px) {
    .item{
      display: flex;
      flex-direction: column;
      width: 100%;
      height: 90vh;
    }
  
    .details_container_item{
      display: flex
  
    }
  
    .item .item_img{
      width: 70vh;
      height: 40vh;
    }
    .container_total_item{
      display: flex;
      box-shadow: 1px 1px 3px 1px rgb(203, 203, 203);
      margin: auto;
      padding: 2vh;
    }
    .border{
      margin-left: 2vh;
      width: 40vh;
    }


  
  }
  